"use client";

import React, { useEffect, useState } from "react";
import { usePathname } from "next/navigation";
import Link from "next/link";
import { House, Users, Locate, Route, Settings, Menu, Sun, Moon } from "lucide-react";
import { useTheme } from "../context/ThemeContext";

const ICONS = {
  House,
  Users,
  Locate,
  Route,
  Settings,
};

// Add this type definition after the imports
type SidebarItem = {
  name: string;
  href: string;
  icon: keyof typeof ICONS;
};

const Sidebar = () => {
  const [isSidebarOpen, setSidebarOpen] = useState(true);
  const [sidebarItems, setSidebarItems] = useState<SidebarItem[]>([]); // Update the type here
  const [isMobile, setIsMobile] = useState(false); // tambah: cek device kecil
  const pathname = usePathname() || ""; // Pastikan pathname tidak undefined
  const { isDarkMode, toggleTheme } = useTheme();

  useEffect(() => {
    const fetchData = async () => {
      try {
        const res = await fetch("/data/data.json");
        if (!res.ok) throw new Error("Failed to fetch data");
        const data = await res.json();
        setSidebarItems(data.sidebarItems);
      } catch (error) {
        console.error("Error fetching sidebar data:", error);
      }
    };

    fetchData();
  }, []);

  useEffect(() => {
    // Fungsi cek ukuran layar
    const handleResize = () => {
      setIsMobile(window.innerWidth < 768); // Tailwind breakpoint md: 768px
    };

    handleResize(); // Cek saat pertama kali render
    window.addEventListener("resize", handleResize); // Update saat window resize
    return () => window.removeEventListener("resize", handleResize);
  }, []);

  // Kalau mobile, sidebar selalu kecil
  const sidebarWidth = isMobile ? "w-20" : isSidebarOpen ? "w-64" : "w-20";

  return (
    <div className={`relative z-10 transition-all duration-300 ease-in-out flex-shrink-0 ${sidebarWidth}`}>
      <div className={`h-full ${isDarkMode ? 'bg-[#1e1e1e]' : 'bg-white'} backdrop-blur-md p-4 flex flex-col border-r ${isDarkMode ? 'border-[#2f2f2f]' : 'border-gray-200'}`}>
        <div className="flex justify-between items-center">
          <button
            onClick={() => !isMobile && setSidebarOpen(!isSidebarOpen)}
            className={`p-2 rounded-full ${isDarkMode ? 'text-white hover:bg-[#2f2f2f]' : 'text-gray-800 hover:bg-gray-100'} transition-colors max-w-fit cursor-pointer`}
          >
            <Menu size={24} />
          </button>

          <button
            onClick={toggleTheme}
            className={`p-2 rounded-full ${isDarkMode ? 'text-white hover:bg-[#2f2f2f]' : 'text-gray-800 hover:bg-gray-100'} transition-colors`}
          >
            {isDarkMode ? <Sun size={20} /> : <Moon size={20} />}
          </button>
        </div>

        <nav className="mt-8 flex-grow">
          {sidebarItems.map((item) => {
            const IconComponent = ICONS[item.icon];
            if (!IconComponent) return null;

            return (
              <Link key={item.name} href={item.href}>
                <div
                  className={`flex items-center p-4 text-sm font-medium rounded-lg transition-colors mb-2 
                    ${isDarkMode 
                      ? 'text-white hover:bg-[#2f2f2f]' 
                      : 'text-gray-800 hover:bg-gray-100'
                    } 
                    ${pathname === item.href 
                      ? isDarkMode ? 'bg-[#2f2f2f]' : 'bg-gray-100' 
                      : ''
                    }`}
                >
                  <IconComponent size={20} style={{ minWidth: "20px" }} />
                  {!isMobile && isSidebarOpen && (
                    <span className="ml-4 whitespace-nowrap">{item.name}</span>
                  )}
                </div>
              </Link>
            );
          })}
        </nav>
      </div>
    </div>
  );
};

export default Sidebar;
